/* Header / Nav Bar */

.nav-bar {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  width: 100%;
  height: 100px;
  background-color: var(--col-green);
  box-shadow: var(--light-shadow);
  overflow: none;
}

.nav-bar .logo {
  font-family: var(--ff-logo);
  font-size: 2rem;
  margin: 0 20px;
  background-color: var(--col-red);
  color: yellow;
  padding: 5px;
  border: 2px solid var(--col-black);
  border-radius: 2px;
  transition: hover 1s;
  box-shadow: var(--light-shadow);
}

.nav-bar .logo:hover {
  cursor: pointer;
  color: var(--col-white);
}
.search-box {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
}

#eyeglass {
  font-size: 1.5rem;
  color: var(--col-off-white);
}

.breed-select {
  min-width: 200px;
  font-family: var(--ff-secondary);
  font-size: 1em;
  margin-right: 5px;
  padding-left: 3px;
  box-shadow: var(--light-shadow);
  cursor: pointer;
}

.search-box #breeds:focus {
  outline: none;
}

.nav-links {
  background-color: white;
  border-radius: 10px;
  padding: 5px;
  margin-right: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  box-shadow: var(--light-shadow);
}

.fa-home,
.fa-shopping-cart {
  font-size: 2rem;
  margin-left: 10px;
  margin-right: 10px;
}

.fas:hover {
  cursor: pointer;
  color: var(--col-orange-red);
}

/* 
-------------
Media Queries
-------------
*/

@media screen and (max-width: 697px) {
  .nav-bar .logo {
    display: none;
  }
}
